    padding: 10px; /* Add padding to create space around the game */
    box-sizing: border-box; /* Include padding in the element's total width and height */
    touch-action: none;
    overflow: hidden;
}

@keyframes typing {
    from { width: 0; }
    to { width: 100%; }
}

@keyframes blink {
    50% { border-color: transparent; }
}

#prompt-container {
    position: fixed;
